import React from 'react';
import {Link} from 'gatsby';
import TournamentPageType from '../../model/tournamentpagetype';

const Header = ({id, pagetype} : {id:string, pagetype:TournamentPageType}) => { 
    const PageLayout = <div className="row">
            <div className="col-md-3 alignCenter">
                <Link to={'/tournaments/' + id} className={pagetype == TournamentPageType.Scores ? "tf-button btn-effect active" : "tf-button btn-effect"}>
                    <span className="boder-fade"></span>          
                    <span className="effect">Scores</span>
                </Link> 
            </div>
            <div className="col-md-3 alignCenter">
                <Link to={'/tournaments/' + id + '/rankings/'} className={pagetype == TournamentPageType.Rankings ? "tf-button btn-effect active" : "tf-button btn-effect"}>
                    <span className="boder-fade"></span>          
                    <span className="effect">Rankings</span>
                </Link> 
            </div>
            <div className="col-md-3 alignCenter">
                <Link to={'/tournaments/' + id + '/players/'} className={pagetype == TournamentPageType.Players ? "tf-button btn-effect active" : "tf-button btn-effect"}>
                    <span className="boder-fade"></span>          
                    <span className="effect">Players</span>
                </Link> 
            </div>
            <div className="col-md-3 alignCenter">
                <Link to={'/tournaments/' + id + '/rules/'} className={pagetype == TournamentPageType.Rules ? "tf-button btn-effect active" : "tf-button btn-effect"}>
                    <span className="boder-fade"></span>          
                    <span className="effect">Rules</span>
                </Link> 
            </div>
        </div>
    return(
        PageLayout
    );
};

export default Header;

